}
.console {
    /* margin: 3% 3vw; */
    width: 94vw;
    width: 100vw;
    z-index: 2;
    height: 94%;
    height: 100%;
    background-color: rgba(36, 32, 44, 0.9);
    background: linear-gradient(148deg, rgba(47, 40, 63, 0.95) 0%, rgba(16, 15, 20, 0.95) 100%);
    /* background: linear-gradient(148deg, rgba(47, 40, 63, 1) 0%, rgba(16, 15, 20, 1) 100%); */
    /* border-radius: 12px; */
    display: flex;
    flex-direction: column;
    /* backdrop-filter: blur(5px); */
}

.consoleHeader {
    width: calc(100% - 20px);
    height: 60px;
    padding-left: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
}

.windowCircle {
    width: 15px;
    height: 15px;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 50%;
    margin-left: 10px;
}

.consoleContent {
    width: calc(100% - 60px);
    flex: 1 1;
    padding: 30px;
    padding-top: 20px;
    font-family: 'Noto Sans Mono';
    font-size: 16px;
    color: rgb(158, 158, 158);
    overflow: hidden;
}

.consoleLine {
    display: flex;
    flex-direction: row;
    margin-bottom: 10px;
}

.Typist  {
    word-break: break-all;
}

.typed {
    color: rgb(221, 221, 221);
}

.highlighted {
    color: rgb(241, 152, 50);
    font-weight: 600;
}

.special {
    color: rgb(71, 197, 128);
    font-weight: 600;
}

.consoleLine a, .link {
    color: rgb(125, 172, 235);
    text-decoration: none;
    cursor: pointer;
    pointer-events: all !important;
}
.headerContent {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
